.preloader {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.lds-ripple {
 display: inline-block;
 position: relative;
 width: 160px;
 height: 160px;
}
.lds-ripple div {
 position: absolute;
 border: 4px solid #fff;
 opacity: 1;
 border-radius: 50%;
 animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
 animation-delay: -0.5s;
}
@keyframes lds-ripple {
 0% {
   top: 86px;
   left: 86px;
   width: 0;
   height: 0;
   opacity: 0;
 }
 4% {
   top: 86px;
   left: 86px;
   width: 0;
   height: 0;
   opacity: 0;
 }
 5% {
   top: 86px;
   left: 86px;
   width: 0;
   height: 0;
   opacity: 1;
 }
 100% {
   top: 0px;
   left: 0px;
   width: 172px;
   height: 172px;
   opacity: 0;
 }
}
